.footerSeparator {
    height:140px;
}

#footer{
    height: 140px;
    border-top: 1px solid white;
    
    max-width: 1280px;
    margin: auto;
    display: grid;
    grid-template-columns: 600px repeat(2, 1fr);
    grid-column-gap: 30px;
    position: relative;
    margin: auto;
}



#footerText{
    margin-top: 20px;
    grid-column: 2/4;
    margin-left:auto;
    margin-right:0;
    align-content: right;
    text-align: right;
    font-family: aktiv-grotesk, sans-serif;
    font-weight: 500;
    font-size: .8em;
    font-style: normal;
    color:white;
}

#barcodeContainer{
    margin-top: 20px;
    grid-column: 1/2;
    height:100%;
}

.barcode {
    width: 264px;
}

@media screen and (max-width: 1024px) {
    .footerSeparator { height:80px; }
    .barcode { width: 300px;}
    #footer{  grid-template-columns: 300px; }
    #footerText{ grid-column: 1/2; text-align: left; }
    #barcodeContainer{ grid-column: 1/2; }
}

