

.strikethrough{
text-decoration: line-through;

}

.red{
color: red ;
}

.blacklisted{
    position: relative;
    display: inline-block;

}

.lvl3Red{
    font-family: t26-carbon, monospace;
    font-weight: 400;
    font-style: normal;
    font-size: 1.1em;
    background-color: #F00000;
    color:black;
}

.grey{
    color: grey;
}

@keyframes blacklistedAnimation {
    from{width:0%;}
    to{width:100%}
}

.blacklisted:before {
        content:"";
        position: absolute;
        width:100%;
        height:100%;
        background-color: "#000000";
        animation: blacklistedAnimation 1s ease-out forwards infinite;
       
}



    