.white{
   background-color: #fff;
   color: #000;
   padding-top: 9px;
   position:relative;
   height: 21px;
   width: 100px;
   margin-right: 8px;
   cursor: pointer;
   
   text-align: center;
   text-decoration: none;
   letter-spacing: .1em;
   line-height: 1em;
   
   font-size: 1em;
   font-family: aktiv-grotesk, sans-serif;
   font-weight: 700;
   font-style: normal; 
   display: inline-block;
   
   
   box-shadow: inset 0 0 0 0 #c0c0c0;
   -webkit-transition: ease-out 0.1s;
   -moz-transition: ease-out 0.1s;
   transition: ease-out 0.1s; 
}

.white:hover {
   box-shadow: inset 100px 0 0 0 #c0c0c0;
}

.black{
    background-color: #000;
    color: #fff;
    padding-top: 8px;
    border: 1px solid white;
    position:relative;
    height: 20px;
    width: 175px;
    cursor: pointer;
    
    text-align: center;
    text-decoration: none;
    letter-spacing: .1em;
    line-height: 1em;
    
    font-size: 1em;
    font-family: aktiv-grotesk, sans-serif;
    font-weight: 700;
    font-style: normal; 
    display: inline-block;
    
    box-shadow: inset 0 0 0 0 #252525;
    -webkit-transition: ease-out 0.1s;
    -moz-transition: ease-out 0.1s;
    transition: ease-out 0.1s; 


}

.black:hover {
   box-shadow: inset 175px 0 0 0 #252525;
}

.center{
    text-align:center;
}