
.textInput{
	height: 30px;
	background-color: #000;
	border: 1px solid #484848;
	box-sizing: border-box;
	color: #fff;
	padding: 5px;
	font-size: 1em;
	font-family: aktiv-grotesk, sans-serif;
	font-weight: 300;
	font-style: normal;
  margin-bottom: 18px;

}

.textInput:focus{
    border: 1px solid #fff;

}

