.button {
    background-color: #fff;
    border: none;
    color: #000;
    padding: 5px;
    text-align: center;
    position:relative;
    height: 30px;
    width: 100px;
    letter-spacing: .1em;
    font-size: 1em;
    font-family: aktiv-grotesk, sans-serif;
    font-weight: 700;
    font-style: normal; 
  box-shadow: inset 0 0 0 0 #c0c0c0;
    -webkit-transition: ease-out 0.1s;
    -moz-transition: ease-out 0.1s;
    
  transition: ease-out 0.1s;
}

.button:hover {
	box-shadow: inset 100px 0 0 0 #c0c0c0;
}

.error{
    color:red;
}

.success{
  color: lawngreen;
}

.enlist{
  line-height: 1.4;
}