/* top div */
.header{
    padding-top: 50px;
	max-width: 1280px;
	margin: auto;
	display: grid;
	grid-template-columns: 100px repeat(11, 1fr);
	grid-column-gap: 30px;
	position: relative;
	margin: auto;
	
}



/* inner div */
.logoArea{
	grid-column: 1/5;

	height: 50px;
	position: relative;
}

.navBar{
	grid-column: 5/13;
	
	position: relative;
	margin-left:auto;
	margin-right:0;
	align-content: right;
	
}

.mobileNav {
	grid-column: 2/3;
	position: relative;
	margin-left:auto;
	margin-right:0;
	margin-top: 0;
	height: 50px;
	

	display: none;
	visibility:hidden;
}

.mobileNavHolder {
	width: 25px;
}

.logo {
	margin-top: 10px;
	float:left;
	width:30px;
}

/* text in above div */
.headerText{
	font-family: aktiv-grotesk, sans-serif;
	font-weight: 500;
	font-style: normal;
	color: #fff;
	margin-top:16px;
	font-size: 2em;
}

.navSeparator {
	grid-column: 1/13;
	height: 1px;
	position: relative;
	border-top: 1px solid #fff;
}

/* mobile navigation menu is its own atom*/

@media screen and (max-width: 1024px) {
	.header{ grid-template-columns: 75% 25%; grid-column-gap: 0px;}
	.logoArea{ grid-column: 1/2; }
	.navSeparator { grid-column: 1/4; }
	.mobileNav {
		grid-column: 2/4;
		display: grid;
		visibility: visible;
	}
	.navBar {
		grid-column: 2/3;
		display:none; 
		visibility:hidden;
	}
}