

.scroll{
    width:150vw;
    position: fixed;
    height:50px;
    overflow: hidden;
    white-space: nowrap;    
    display: flex;
    background-color: #F00000;
    z-index: 9999;
    align-items: center;
    opacity:0;
    animation-name: entryAnim;
    animation-duration: 3s;
    animation-direction: normal;
    animation-timing-function: linear;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
    -webkit-animation-fill-mode: forwards;
}

.breakers {
    position: fixed;
    height:30px;
    width: 300px;
    color:#fff;
    z-index: 9998;
    margin-top: 30px;
    margin-left: 1000px;
    background-color: #F00000;
    animation-name: additiveGlitchAnim1;
    animation-duration: 5s;
    animation-direction: normal;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
    animation-fill-mode: forwards;
    -webkit-animation-fill-mode: forwards;
}

.breakers2 {
    position: fixed;
    height:30px;
    width: 900px;
    color:#fff;
    z-index: 9998;
    margin-top: -20px;
    margin-left: 500px;
    background-color: #F00000;
    animation-name: additiveGlitchAnim2;
    animation-duration: 6s;
    animation-delay: 1s;
    animation-direction: normal;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
    animation-fill-mode: forwards;
    -webkit-animation-fill-mode: forwards;
}

.breakers3 {
    position: fixed;
    height:30px;
    width: 100px;
    color:#fff;
    z-index: 9998;
    margin-top: -20px;
    margin-left: 1450px;
    background-color: #F00000;
    animation-name: additiveGlitchAnim3;
    animation-duration: 6s;
    animation-delay: 1s;
    animation-direction: normal;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
    animation-fill-mode: forwards;
    -webkit-animation-fill-mode: forwards;
}

.breakers4 {
    position: fixed;
    height:30px;
    width: 100px;
    color:#fff;
    z-index: 9998;
    margin-top: -20px;
    margin-left: 350px;
    background-color: #F00000;
    animation-name: additiveGlitchAnim4;
    animation-duration: 6s;
    animation-delay: 1s;
    animation-direction: normal;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
    animation-fill-mode: forwards;
    -webkit-animation-fill-mode: forwards;
}




.scrollText{
    padding-left:100%;
    padding-bottom: 60px;
    font-family: t26-carbon, monospace;
    font-weight: 400;
    font-style: normal;
    font-size: 4em;
    color: black;
    white-space: nowrap;
    text-transform: uppercase;
    animation: animate1 20s linear infinite;
}

.scrollText2{
    margin-right: 100px;
    padding-bottom: 60px;
    font-size: 3em;
    font-family: t26-carbon, monospace;
    font-weight: 400;
    font-style: normal;
    color: black;
    white-space: nowrap;
    text-transform: uppercase;
    animation: animate2 5s linear infinite;
}

.leftRotateTop{
    position:fixed;
    z-index: 9998;
    transform: rotate(5deg) translateX(calc(50vw * -1));
}

.rightRotateBottom{
    position:fixed;
    z-index: 9998;
    top:80%;
    transform: rotate(-5deg) translateX(calc(50vw * -1));  
}

.leftRotateBottom{
    width:150vw;
    transform: rotate(5deg) translateX(calc(50vw * -1));  
}

.rightRotate{
    width:150vw;
    transform: rotate(-5deg) translateX(calc(50vw * -1));  
}

@keyframes entryAnim {
    0% { 
        opacity:1;

        -webkit-clip-path: polygon(0 20%, 100% 20%, 100% 21%, 0 21%);
        clip-path: polygon(0 20%, 100% 20%, 100% 21%, 0 21%);
    }
    2% {
        -webkit-clip-path: polygon(0 33%, 100% 33%, 100% 33%, 0 33%);
        clip-path: polygon(0 33%, 100% 33%, 100% 33%, 0 33%);
    }
    4% {
        -webkit-clip-path: polygon(0 44%, 100% 44%, 100% 44%, 0 44%);
        clip-path: polygon(0 44%, 100% 44%, 100% 44%, 0 44%);
    }
    5% {
        -webkit-clip-path: polygon(0 50%, 100% 50%, 100% 20%, 0 20%);
        clip-path: polygon(0 50%, 100% 50%, 100% 20%, 0 20%);
    }
    6% {
        -webkit-clip-path: polygon(0 70%, 100% 70%, 100% 70%, 0 70%);
        clip-path: polygon(0 70%, 100% 70%, 100% 70%, 0 70%);
    }
    7% {
        -webkit-clip-path: polygon(0 80%, 100% 80%, 100% 80%, 0 80%);
        clip-path: polygon(0 80%, 100% 80%, 100% 80%, 0 80%);
    }
    8% {
        -webkit-clip-path: polygon(0 50%, 100% 50%, 100% 55%, 0 55%);
        clip-path: polygon(0 50%, 100% 50%, 100% 55%, 0 55%);
    }
    9% {
        -webkit-clip-path: polygon(0 70%, 100% 70%, 100% 80%, 0 80%);
        clip-path: polygon(0 70%, 100% 70%, 100% 80%, 0 80%);
    }

    10%, 100% {
        opacity:1;

        -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
        clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
    }
}

@keyframes additiveGlitchAnim1
{
    0%{
        opacity: 1;
      margin-left: 200px;  
    }
    .2% {
        margin-left: 300px;
        width: 500px;
    }
    .3% {
        margin-left: 600px;
        width: 500px;
    }
    2% {
        margin-left: 600px;
    }
    4.9% {
        margin-left: 600px;
    }
    5% {
        margin-left: 1000px;
        
    }
    5.9% {
        opacity: 1;
    }
    6% {
        opacity: 0;
    }
    8%{
        margin-left:1200px;
        opacity:0;
    }
    8.1% {
        opacity: 1;
    }
    9% {
        margin-left: 1200px;
    }
    9.1% {
        margin-left: 1000px;
    }
    100%{
        opacity: 1;
        margin-left: 1000px;
        width: 500px;
    }
    
}

@keyframes additiveGlitchAnim2
{
    0%{
        margin-left: 700px;
    }
    
    20% {
        margin-left: 700px;
    }
    20.1% {
        margin-left: 100px;
    }
    100%{
        opacity: 1;
        margin-left: 100px;
    }
    
}

@keyframes additiveGlitchAnim3
{
    0%{
        margin-left: 1650px;
    }
    
    20% {
        margin-left: 1650px;
        opacity:1;
    }
    20.1% {
        opacity:0;
        margin-left: 1050px;
    }
    20.4% {
        opacity:1;
    }
    100%{
        opacity: 1;
        margin-left: 1050px;
    }
    
}

@keyframes additiveGlitchAnim4
{
    0%{
        margin-left: 550px;
    }
    
    20% {
        margin-left: 550px;
        opacity:1;
    }
    20.1% {
        opacity:0;
        margin-left: -50px;
    }
    20.4% {
        opacity:1;
    }
    100%{
        opacity: 1;
        margin-left: -50px;
    }
    
}

@keyframes animate1
{
    0%{
        transform: translateY(300px) translateX((600px)); 
    }
    
   
    
    100%{
        transform: translateX(-100%);
    }
}

@keyframes animate2
{
    0%{
        transform: translateY(300px) translateX((-100%));

        
    }
    100%{
        transform: translateX(600px);
        
    }
}