

.activeTab{
    border-bottom:2px white solid;
    display: flex;
    align-items: center;
    cursor: pointer;
    margin-right:10px
}

.navTabText{
    text-decoration: none;
    color: white;
}

.navTab{
    background-color: transparent;
	height: 50px;
	border: 2px solid #000;
	font-family: aktiv-grotesk, sans-serif;
	font-weight: 500;
	font-size: 1em;
	font-style: normal;
	letter-spacing: .1em;
	color: #fff;
	padding-right:10px;
	cursor:pointer;
	-webkit-transition: ease-out 0.2s;
	  -moz-transition: ease-out 0.2s;
	  transition: ease-out 0.2s;

}

.navTab:hover {
	background-color: transparent;
	height: 50px;
	border-bottom: 2px solid #fff;
}

.navTab:visited{
    text-decoration: none;
}


.button {
    background-color: #fff;
    border: none;
    color: #000;
    padding: 5px;
    text-align: center;
    position:relative;
    height: 30px;
    width: 100px;
    letter-spacing: .1em; 
    font-size: 2em;
    font-family: aktiv-grotesk, sans-serif;
    font-weight: 700;
    font-style: normal; 
    box-shadow: inset 0 0 0 0 #c0c0c0;
    -webkit-transition: ease-out 0.1s;
    -moz-transition: ease-out 0.1s;
    
  transition: ease-out 0.1s;
}

.button:hover {
	box-shadow: inset 100px 0 0 0 #c0c0c0;
}

.buttonText{
    color: black;
    text-decoration: none;
}

