
body {
    background-color: #000;
}

h1 {
	color: #fff;
	font-size: 4.1em;
	font-family: aktiv-grotesk, sans-serif;
	font-weight: 500;
	font-style: normal;
}



h2 {
	color: #fff;
	letter-spacing: .1em;
	font-size: 1.6em;
	font-family: aktiv-grotesk, sans-serif;
	font-weight: 700;
	font-style: normal;
}

p {
	color: #fff;
	letter-spacing: .1em;
	line-height: 1.5em;
	font-size: 1.1em;
	font-family: aktiv-grotesk, sans-serif;
	font-weight: 300;
	font-style: normal;
}

img {
  width: 100%;
  margin-top: 80px;
}

/* Home tags */
.Home{

}

.video{
width: 100%;
height: 300px;
}

.HomeText{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    margin-right:10%;
    margin-left:10%;
    border-left: 1px solid black;
    color: white;
    margin-bottom: 191px;
}

/* Answer Tags */
.baseGrid{
    max-width: 1280px;
	  margin: 0, auto;
	  display: grid;
	  grid-template-columns: 250px repeat(11, 1fr);
	  grid-column-gap: 50px;
	  position: relative;
	  margin: auto;

}

.mainBody{
	  padding-top: 100px;
	  grid-column: 1 / 6;
    color: white;
    @media screen and (max-width: 1024px) {
      grid-column: 1/13; 
    }

}

.mainHeading{
    margin-top: 3rem;
    color:white;
    display:flex;
    flex-direction: row;
    align-items: center;
    margin-left:10rem;
}

.headerHolder {
  width: 100%;
  margin-bottom: 20px;
}

.answerHeadingDiv{
    background-color: white;
    height:1.6rem;
    width:12rem;
    margin-left:1rem;
}


#answerBlock{
    color: white;
    display:flex;
    flex-direction: column;
    width:40%;
    margin-left: 1.5rem;
    justify-content: center;
}

.signupModule {
    color: white;
    grid-column: 6 / 13;
	  position: absolute;
	  bottom: 0;
	  border-left: 1px solid #444;
	  padding-left: 15px;
    
}






/* Mailing Tags */

#mailing{
    margin-top:3rem;
    margin-bottom:10rem;
}

#mailingText{
    color:white;
   
}

#mailingForm{
    color:white;
    
}

#mailingHeading{
}


/* Enlist */

.enlistBoxDisplay {
    grid-column: 6 / 13;
    top:0;
}


.saleItemBlock {
  margin: 0, auto;
  display: grid;
  grid-template-columns: 80% 20%;
  grid-column-gap: 0px;
  position: relative;
  margin: auto;
  border-top: 1px solid #fff;
}



.saleItemDescription {
  grid-column: 1/2;
  margin-top: 20px;
  margin-bottom: auto;
}

.saleItemButton {
  grid-column: 2/3;
  margin-top: auto;
  margin-bottom: auto;
  margin-left: auto;
  margin-right:0;
}

/* General Tags */
.hackableTextBlock{
    line-height: 1.5em;
	  margin-bottom: 50px;
  
    
}

.buttonRow{
    margin-top: 20px;
    display:flex;
    flex-direction: row;
}

.video{
    margin-top:3em;
    width:100%;
    height:600px;
    display:flex;
    justify-content: center;
    align-items: center;
    margin-bottom:-3em;
}
.videoHome {
height: 100%;
width:100%;
}





@media screen and (max-width: 1360px) {
  body { margin-left: 40px; margin-right:40px; }
}

@media screen and (max-width: 1024px) {
  .headerHolder { width: 350px; margin-bottom: 40px; }
  .baseGrid { max-width: 700px; grid-template-columns: 100%; }
  .mainBody { grid-column: 1/2; }
  .video { height:250px; }
  .signupModule { grid-column: 1/2; position: relative; border-left: 0px solid #000; border-top: 4px solid #fff; padding-left: 0px; margin-top: 40px;}
  .enlistBoxDisplay { grid-column: 1/2; }
  .saleItemBlock { grid-template-columns: 100%; }
  .saleItemDescription { grid-column: 1/2; }
  .saleItemButton { grid-column: 1/2;   margin-bottom: 30px; margin-left:0; margin-right: auto;}
}
