

.logoArea {
	grid-column: 1/2;
	height: 30px;
	position: relative;
}

.closeOut {
	grid-column: 2/3;
	height: 30px;
	position: relative;
	margin-right:0;
	margin-left:auto;
}

.navSeparator {
	grid-column: 1/3;
	height: 1px;
	position: relative;
	border-top: 1px solid #fff;
}

.navArea {
	position: relative;
	margin-left:0;
	max-height: 0px;
	overflow: hidden;
	align-content: left;
    display: flex;
    flex-direction: column;	
	transition: all ease-out 0.5s;
}

.navArea.menu-active {
	position: relative;
	margin-left:0;
	max-height: 300px;
	overflow: hidden;
	align-content: left;
	display: flex;
	flex-direction: column;	
	transition: all ease-out 0.5s;
}



.mobileNavLink {
	margin-top: 20px;
	background-color: transparent;
	width: 100vw;
	text-align: left;
	text-decoration: none;
	border: 2px solid #000;
	font-family: aktiv-grotesk, sans-serif;
	font-weight: 400;
	font-size: 3.5em;
	font-style: normal;
	letter-spacing: .1em;
	color: #fff;
	cursor:pointer;

}